import React from "react";
import "../../sass/Home.scss";




function Home({ setPage }) {
  return (
    <div className="home">
      <div className="hero">
        <h1>HI THEREEEE!!!</h1>
        <p>Manage contacts and accounts in one simple place.</p>
        <button onClick={() => setPage("signup")}>Get Started</button>
      </div>
    </div>
  );
}

export default Home;
