import React, { useEffect, useState } from "react";
import "../../sass/Contact.us.scss";



function Contact() {
  const API_URL = "http://127.0.0.1:8000/api/contacts";

  const [contacts, setContacts] = useState([]);
  const [form, setForm] = useState({ name: "", email: "", message: "" });
  const [editingId, setEditingId] = useState(null);
  const [status, setStatus] = useState("");
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    fetchContacts();
  }, []);

  const fetchContacts = async () => {
    try {
      const res = await fetch(API_URL);
      const data = await res.json();
      setContacts(data);
    } catch (err) {
      console.error(err);
    }
  };

  const handleChange = (e) => setForm({ ...form, [e.target.name]: e.target.value });

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setStatus("");

    try {
      const res = await fetch(editingId ? `${API_URL}/${editingId}` : API_URL, {
        method: editingId ? "PUT" : "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form),
      });

      const data = await res.json();
      if (!res.ok) throw new Error(data.message || "Error saving contact");

      setStatus(editingId ? "✅ Contact updated!" : "✅ Contact added!");
      setForm({ name: "", email: "", message: "" });
      setEditingId(null);
      fetchContacts();
    } catch (err) {
      console.error(err);
      setStatus("❌ " + err.message);
    } finally {
      setLoading(false);
    }
  };

  const handleEdit = (c) => {
    setForm({ name: c.name, email: c.email, message: c.message });
    setEditingId(c.id);
  };

  const handleDelete = async (id) => {
    if (!window.confirm("Are you sure?")) return;

    try {
      await fetch(`${API_URL}/${id}`, { method: "DELETE" });
      setStatus("✅ Contact deleted!");
      fetchContacts();
    } catch (err) {
      console.error(err);
      setStatus("❌ Error deleting contact");
    }
  };

  return (
    <div className="contact-page">
      <h2>Contact Manager</h2>

      <div className="form-card">
        <form onSubmit={handleSubmit}>
          {["name", "email"].map((field) => (
            <div className="input-group" key={field}>
              <input
                type={field === "email" ? "email" : "text"}
                name={field}
                value={form[field]}
                onChange={handleChange}
                required
                autoComplete="off"
              />
              <label>{field.charAt(0).toUpperCase() + field.slice(1)}</label>
            </div>
          ))}

          <div className="input-group">
            <textarea
              name="message"
              value={form.message}
              onChange={handleChange}
              required
            />
            <label>Message</label>
          </div>

          <button type="submit" className={loading ? "loading" : ""} disabled={loading}>
            {loading ? (editingId ? "Updating..." : "Adding...") : editingId ? "Update Contact" : "Add Contact"}
          </button>

          {status && <p className={`status ${status.includes("✅") ? "success" : "error"}`}>{status}</p>}
        </form>
      </div>

      <div className="list-card">
        <h3>All Contacts</h3>
        <table>
          <thead>
            <tr>
              <th>Name</th>
              <th>Email</th>
              <th>Message</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {contacts.length > 0 ? (
              contacts.map((c) => (
                <tr key={c.id}>
                  <td>{c.name}</td>
                  <td>{c.email}</td>
                  <td>{c.message}</td>
                  <td>
                    <button className="edit" onClick={() => handleEdit(c)}>Edit</button>
                    <button className="delete" onClick={() => handleDelete(c.id)}>Delete</button>
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan="4" style={{ textAlign: "center" }}>No contacts found.</td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default Contact;
